import {React, Immutable} from '../../common/Util';
import * as actionTypes from '../actions/actionTypes';

const ToolbarView = React.createClass({
    gotoGraph: function () {
        let list = this.props.list.filter(item=>item.get('checked')).map(item=>item.get('dfhm') + '_phoneNO');
        list = Immutable.Set(list).toJS();
        mining.utils.showPhoneGraph(list, 'graph');
    },
    gotoMapping: function () {
        let list = this.props.list.filter(item=>item.get('checked')).map(item=>item.get('key')).toJS();
        list = Immutable.Set(list).toJS();
        mining.utils.showPhoneGraph(list, 'map');
    },
    render: function () {
        let toTopCls = 'totop';
        if (this.props.merge) {
            toTopCls += ' hide';
        }
        if (this.props.checkedToTop) {
            toTopCls += ' active';
        }
        return (
            <div className="toolbar">
                <div className="backoff" title="" data-logid="200" data-logcontent="---" onClick={this.props.changeStepBack}></div>
                <div className="forward" title="" data-logid="201" data-logcontent="---" onClick={this.props.changeStepForward}></div>
                <div className={this.props.merge ? "highlight hide" : "highlight"} title="" data-logid="202" data-logcontent="---"
                     onClick={this.props.setHighlightChecked}></div>
                <div className={!this.props.merge ? "combine" : "combine active"}
                     title={!this.props.merge ? "" : ""} data-logid="203" data-logcontent="---"
                     onClick={()=>this.props.changeMerge(!this.props.merge)}></div>
                <div className={toTopCls} title="" data-logid="204" data-logcontent="---"
                     onClick={this.props.setCheckedToTop}></div>
                <div className="delete" title="" data-logid="205" data-logcontent="---" onClick={this.props.delChecked}></div>
                <div className="oppositeDelete" title="" data-logid="206" data-logcontent="---" onClick={this.props.delReverseChecked}></div>
                <div className={this.props.merge ? 'setmachinephone hide' : 'setmachinephone'} title="" data-logid="207" data-logcontent="---"
                     onClick={this.props.getMachinePhoneData}></div>
                <div className="rightbtns">
                    <button title="" className="btn-current" data-logid="208" data-logcontent="---" onClick={this.gotoGraph}></button>
                    <button title="" className="btn-current" data-logid="209" data-logcontent="---" onClick={this.gotoMapping}></button>
                </div>
            </div>
        );
    }
});

export default ToolbarView;


